body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.mm-game__board {
  min-width: 160px;
}

.mm-status {
  margin-bottom: 10px;
  min-height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.mm-commands {
  min-height: 28px;
}

.mm-commands div {
}

.board-row.active .square:not(.mm-dots) {
  border: 1px solid #000;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 44px;
  height: 44px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 44px;
}

.square:focus {
  outline: none;
}

.square.mm-dots {
  border-color: transparent;
  margin-left: 1px;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.mm-game {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 550px;
  margin: 0 auto;
}

.mm-game__logo {
  flex-basis: 100%;
  overflow: hidden;
}

.mm-game__logo img {
  width: 100%;
}

.mm-game__board {
  flex-basis: 280px;
  margin-bottom: 10px;
}

.mm-selection {
  margin: 10px 0;
  table-layout: fixed;
  width: 184px;
  border-collapse: collapse;
}

.mm-selection td {
  width: 46px;
}

.mm-selection td button {
  width: 100%;
}

.mm-icon:before {
  font-size: 24px;
  font-family: 'Playing Cards', serif;
}

.mm-icon--0:before {
  content: "\e900";
}

.mm-icon--1:before {
  content: "\e903";
}

.mm-icon--2:before {
  content: "\e902";
  color: red;
}

.mm-icon--3:before {
  content: "\e901";
  color: red;
}

.mm-icon--2:disabled:before,
.mm-icon--2[disabled]:before,
.mm-icon--3:disabled:before,
.mm-icon--3[disabled]:before {
  opacity: .4;
}

.mm-dot {
  line-height: 10px;
  display: block;
  text-align: left;
  padding-left: 6px;
  padding-top: 4px;
}

.mm-dot--red {
  color: red;
}

.mm-game__info {
  flex: 1;
  justify-content: flex-end;
  padding-top: 25px;
}

.mm-game__info img {
  max-width: 200px;
  display: block;
}

@font-face {
  font-family: "Playing Cards";
  src: local("AssistantRegular"),
  url("./fonts/icomoon/fonts/icomoon.ttf") format("truetype");
  font-weight: normal;
}
